/** Media Queries **/ /* Globales
----------------------------------------------------------*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #f8f8f8;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  overflow-x: hidden;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
  height: auto;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

/* Botones
----------------------------------------------------------*/
.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

/* Header
----------------------------------------------------------*/
.header {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__logo {
  padding-bottom: 3rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    padding-bottom: unset;
  }
}

/* Sociales
----------------------------------------------------------*/
.sociales {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}
.sociales__enlace::before {
  content: "";
  padding: 2rem;
  display: inline-block;
}
.sociales__enlace[href*="facebook.com"]::before {
  background-image: url(../img/icono_facebook.svg);
}
.sociales__enlace[href*="twitter.com"]::before {
  background-image: url(../img/icono_twitter.svg);
}
.sociales__enlace[href*="instagram.com"]::before {
  background-image: url(../img/icono_instagram.svg);
}
.sociales__enlace[href*="youtube.com"]::before {
  background-image: url(../img/icono_youtube.svg);
}
.sociales__enlace[href*="tiktok.com"]::before {
  background-image: url(../img/icono_tiktok.svg);
}
.sociales__accesible {
  overflow: hidden;
  width: 1px;
  height: 1px;
  margin: -1px;
  display: block;
}

/* Navegacion
----------------------------------------------------------*/
.navegacion {
  padding: 0.3rem 0;
  margin: 5rem 0;
  background-color: #8ed3cc;
}
@media (min-width: 768px) {
  .navegacion {
    margin: 2rem 0;
  }
}
.navegacion__contenido {
  border-block: 2px dashed #FFFFFF;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .navegacion__list {
    display: flex;
    justify-content: space-between;
  }
}
.navegacion__item {
  line-height: 1.7;
  text-align: center;
}
.navegacion__enlace {
  color: #FFFFFF;
  font-size: 2.4rem;
}

.swiper .swiper-wrapper {
  cursor: grab;
}
.swiper .swiper-wrapper:active {
  cursor: grabbing;
}
.swiper .swiper-button-prev::after,
.swiper .swiper-button-next::after {
  display: none;
}
@media (min-width: 768px) {
  .swiper .swiper-button-prev::after,
  .swiper .swiper-button-next::after {
    color: #FFFFFF;
    display: inline-block;
    font-size: 3rem;
  }
}
.swiper .swiper-pagination-bullet {
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .swiper .swiper-pagination-bullet {
    display: none;
  }
}
.swiper .swiper-pagination-bullet-active {
  background-color: #FFFFFF;
}
.swiper .swiper-img {
  height: 22rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .swiper .swiper-img {
    height: unset;
  }
}

/* Bienvenida
----------------------------------------------------------*/
.bienvenida {
  text-align: center;
}
.bienvenida__heading {
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  margin-bottom: 4rem;
  font-size: 4rem;
}
.bienvenida__heading > * {
  display: block;
  font-weight: 300;
  font-size: 4rem;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  color: initial;
}
.bienvenida__texto {
  font-size: 2.6rem;
  color: #979797;
  max-width: 50rem;
  margin: 0 auto;
  line-height: 1.8;
}

/* Bienvenida
----------------------------------------------------------*/
.servicios__grid {
  width: 85%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .servicios__grid {
    width: 100%;
    margin: unset;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.card__imgContainer {
  background-color: #FFFFFF;
  padding: 0.5rem;
  transition-property: rotate scale;
  transition-duration: 300ms;
}
.card__imgContainer:hover {
  transform: scale(1.1) rotate(7deg);
}
.card__info {
  max-width: 90%;
  margin: 0 auto;
  background-color: #FFFFFF;
  text-align: center;
  transform: translateY(-5rem);
}
.card__heading {
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  margin-bottom: 4rem;
  font-size: 4rem;
  padding: 2rem 0;
  margin: 0;
  font-size: 3.5rem;
}
.card__heading > * {
  display: block;
  font-weight: 300;
  font-size: 4rem;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  color: initial;
}
.card__span {
  font-size: 3rem;
}
@media (min-width: 768px) {
  .card__span {
    font-size: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .card__span {
    font-size: 3.5rem;
  }
}
.card__btn {
  background-color: #e082a8;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  display: block;
}
@media (min-width: 768px) {
  .card__btn {
    width: auto;
  }
}
.card__btn:hover {
  background-color: #d6598c;
}

/* Horarios
----------------------------------------------------------*/
@media (min-width: 768px) {
  .horarios {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.horarios__info {
  background-color: #8ed3cc;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .horarios__info {
    padding: 5rem;
  }
}
.horarios__heading {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 4rem;
}
.horarios__descripcion {
  text-align: center;
  font-size: 2rem;
  line-height: 2;
}
.horarios__img {
  height: 35rem;
  background-image: url(../img/bg_horarios.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right center;
}
@media (min-width: 768px) {
  .horarios__img {
    height: auto;
    background-position: -130rem center;
  }
}
@media (min-width: 1024px) {
  .horarios__img {
    background-position: right center;
  }
}

.tabla {
  width: 100%;
  border: 0.5rem solid #e082a8;
  text-align: center;
  border-collapse: collapse;
}
.tabla__thead {
  background-color: #e082a8;
}
.tabla__th {
  padding: 2rem;
}
.tabla__tr:hover {
  background-color: #69c5bb;
}
.tabla__td {
  padding: 1rem;
  font-size: 2.2rem;
}

/* Productos
----------------------------------------------------------*/
.productos__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  margin-bottom: 4rem;
  font-size: 4rem;
}
.productos__heading > * {
  display: block;
  font-weight: 300;
  font-size: 4rem;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  color: initial;
}
.productos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.producto__imgContainer {
  overflow: hidden;
}
.producto__img {
  transition-property: transform;
  transition-duration: 250ms;
}
.producto__img:hover {
  transform: scale(1.2);
}
.producto__info {
  background-color: #FFFFFF;
  padding: 2rem;
}
.producto__heading {
  text-transform: uppercase;
  color: #e082a8;
  text-align: center;
  font-weight: 400;
  margin-top: 0;
}
.producto__description {
  text-transform: uppercase;
  font-size: 1.6rem;
  line-height: 1.8;
  text-align: center;
}
.producto__precio {
  text-align: center;
  font-size: 2.8rem;
  margin: 0;
  color: #979797;
}

/* Cita
----------------------------------------------------------*/
.cita {
  text-align: center;
  background-image: url(../img/bg_citas.jpg);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 5rem 0;
  background-attachment: fixed;
}
.cita__heading, .cita__texto {
  color: #FFFFFF;
}
.cita__heading {
  text-transform: uppercase;
  font-size: 5rem;
  font-weight: 400;
}
@media (min-width: 768px) {
  .cita__heading {
    font-size: 6rem;
  }
}
.cita__texto {
  line-height: 2;
  margin: 0 auto;
  width: 95%;
}
@media (min-width: 768px) {
  .cita__texto {
    width: 60rem;
  }
}
.cita__btn {
  background-color: #8ed3cc;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  padding-inline: 10rem;
}
@media (min-width: 768px) {
  .cita__btn {
    width: auto;
  }
}
.cita__btn:hover {
  background-color: #69c5bb;
}

/* Footer
----------------------------------------------------------*/
.footer__grid {
  padding: 5rem 0 2rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__seccion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer__seccion {
    margin-bottom: 0;
  }
}
.footer__heading {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 3.4rem;
}
.footer__texto {
  line-height: 1.8;
  text-align: center;
  margin: 0 0 2rem 0;
}

.copyright {
  text-align: center;
  border-top: 1px solid #d7d7d7;
  max-width: 120rem;
  margin: 0 auto;
}
.copyright__texto {
  padding: 2rem;
  color: #979797;
  margin: 0;
  font-size: 1.6rem;
}